// icon-font
.icon {
  width: 1em; height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}

.text-ellipsis {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.home-page {
  height: calc(100vh - 50px);
  .am-tab-bar {
    height: 100vh;
  }
  .am-tabs-tab-bar-wrap {
    // height: .9rem;
    .am-tab-bar-tab-title {
      // font-size: .18rem;
    }
  }
}

.place-order-stepper {
  padding: 0 !important;
  width: 100% !important;
  height: .35rem !important;
  line-height: .35rem !important;
  border: 1px solid #F0F0F0;
  border-radius: 3px;
  .am-stepper-handler {
    border: 0;
    color: #0078FF;
  }
  .am-icon-xxs {
    width: .12rem;
    height: .12rem;
  }
  .am-stepper-input {
    position: relative;
    // left: -.02rem;
    top: .02rem;
    width: .55rem !important;
    font-size: .12rem;
  }
}


