.am-list-body::before {
  display: none !important;
}

.pair-searchbar {
  .am-search {
    padding: 0;
    background-color: transparent;
    .am-search-input {
      border: 1px solid #E6E6E6;
    }
  }
}

.signin-modal,
.upsert-password-modal {
  .am-modal-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.gas-slider {
  .am-slider-dot {
    margin-left: -9px !important;
    width: 10px !important;
    height: 10px !important;
  }
}

.am-activity-indicator {
  justify-content: center;
}

.tvchart-ops {
  position: relative;
  flex-basis: 25%;
  text-align: center;
  &.with-corner::after {
    position: absolute;
    top: .06rem;
    margin-left: .03rem;
    content: ' ';
    width: 0;
    height: 0;
    border-bottom: .05rem solid #646464;
    border-left: .05rem solid transparent;
  }
}
